.tables {
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
    width: 100%;
    height: 95.5vh;
    top: 0;
    padding: 0;
    bottom: 0;
}

@media (max-width: 540px) {
    .tables {
        display: inline-block;
        overflow-y: auto;
        overflow-x: hidden;
    }
}